html {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);



  font-family: "Times New Roman";
  font-size: 14px;
  font-style: normal;
  font-variant: normal;
  font-weight: 700;
  line-height: 13.2px;
  padding-top: 50px;
  padding-bottom: 100px;
}

img {
  display: block;
  margin: auto;
}